@use '../abstracts' as *;

.input {
  padding-inline: rem(74) rem(14);
  padding-block: rem(14);

  border: none;
  border-radius: rem(5);
  outline: none;

  box-shadow: 0px 2px 9px 0px hsla(0, 0%, 0%, 0.05);
  background-color: var(--clr-neutral-300);

  &__label {
    position: relative;

    height: 100%;

    &:before {
      position: absolute;
      content: url('../../assets/search.svg');
      top: 50%;
      translate: 0 -30%;
      left: rem(32);

      filter: brightness(0) saturate(100%) invert(67%) sepia(0%) saturate(20%)
        hue-rotate(33deg) brightness(107%) contrast(92%);
    }
  }
}
